.wp-block-widget-werstreamtes {

  border-radius: 4px;
  padding: 10px;

  &.is-selected {
    box-shadow: 0 0 3px #ddd;
  }

  iframe {
    width: 100%;
    pointer-events: none;
    min-height: 400px;
  }


  &:focus:after {
    all: unset !important;
  }

  small {
    font-size: 11px;

    span {
      background: #302B25;
      color: #fff;
      word-break: break-all;
      padding: 2px 4px;
      border-radius: 3px;

      &:first-child {
        margin-left: 5px;
      }

      &:not(:last-child) {
        margin-right: 5px;
      }

    }

  }

  .components-tab-panel__tabs {

    margin-bottom: 10px;
    margin-top: 15px;

    .components-tab-panel__tabs-item {

      border: 1px solid #000;
      border-radius: 3px;
      height: 30px;

      &.active-tab {
        background: #302B25;
        color: #fff;
      }

      &:not(:last-child) {
        margin-right: 10px;
      }

    }

  }

  a {
    color: #dfad06;
  }

  p {
    font-size: 12px !important;
    margin-bottom: 0 !important;
    color: #000;
  }

}

.wse-content-item {

  img {
    float: left;
    margin-right: 10px;
    border-radius: 2px;
    height: 45px;
  }

  div {

    float: left;

    strong {
      margin-bottom: 3px;
      font-size: 14px;
      display: block;
      margin-top: 5px;
    }

    small {
      display: block;
      color: #3b3b3b;
    }

    &:after {
      content: "";
      display: block;
      clear: left;
    }
  }

}

.components-autocomplete__popover {

  button {
    display: block !important;
  }

  .components-popover__content {
    min-width: 350px;
  }

}